import React from 'react';
import { Sum } from './components/Sum/Sum';
import './App.css';

export const App = () => {
  const numbers = [
    [2, 3],
    [-5, 5],
    [10, 0],
    [0, 5],
    [0, 0],
  ];

  return (
    <>
      {numbers.map(([a, b]) => (
        <Sum
          a={a}
          b={b}
        />
      ))}
    </>
  );
};
